<script>
export default {
  data() {
    return {
      roll: "",
      compr: false,
    };
  },
  mounted() {
    this.roll = JSON.parse(localStorage.getItem("userControl")).rol;

    this.compr = this.comp();
  },
  methods: {
    goToRegister() {
      this.$router.push({
        name: "register",
        params: {},
      });
    },
    comp() {
      return this.roll == "Admin";
    },
    cerrarSesion() {
      localStorage.setItem("userControl", null);
      localStorage.setItem("userLogged", null);
      this.$router.push({
        name: "Home",
        params: {},
      });
    },
  },
};
</script>

